import React, { useRef, useState } from "react";

// search_module.scss
import "../../css/search_module.scss";
//
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faSearch } from "@fortawesome/free-solid-svg-icons";
//    
// import SearchingCat from "./SearchingCat";

//   
import { wNew, wSale } from "../data/products_woman";
import { mBestSeller, mNew, mOrigins, mSale } from "../data/products_man";
import SearchModuleList from "./SearchModuleList";

// import { wNew } from "../data/swiper_cat";

function SearchModule({ kword }) {
  //(topcate ) 
  const [check, setCheck] = useState([true, true, true, true]); //useState:true,ture,ture(,,)
  console.log("^^^^^^^^^", check);
  //  
  const selData = [
    ...wNew,
    ...wSale,
    ...mBestSeller,
    ...mNew,
    ...mSale,
    ...mOrigins,
  ];
  // console.log(" selData  ?",selData);

  //  
  const mybutton = ["", "", "", "HOME"];
  //  
  const [onbutton, setOnbutton] = useState(""); // 
  console.log("^^^^^", onbutton);

  //   
  const [sort, setSort] = useState("newpd");
  // console.log(sortState);
  //      !

  //()   .
  // kword-  
  console.log("( ):", kword);
  const [searchword, setSearchword] = useState(kword);

  const beforeWord = useRef(kword);
  console.log(kword, "==?", beforeWord.current);
  //          
  //    ! ->  !
  if (kword != beforeWord.current) {
    console.log("!!!");
    //    :  
    setSearchword(kword);
    //     
    beforeWord.current = kword;
    //    
    document.querySelector("#schin").value = kword;
    //    
    document.querySelector("#schinGnb").value = "";
    document.querySelector("#schinGnb").blur();
  }

  //   
  ////////////filter///////////////////////////
  const newList = selData.filter((v) => {
    //  v.name  
    let lowerVname = v.name.toLocaleLowerCase();

    //  ( )  
    let lowerSearchWord = searchword.toLocaleLowerCase();

    // name undefined  
    const itemName = lowerVname || "";

    if (
      // name    
      itemName.includes(lowerSearchWord) && //<-&&  true.
      ((check[0] ? v.topcate == "woman" : false) || //check[0] ? v.topcate=="woman"  false)
        (check[1] ? v.topcate == "man" : false) || //check[1] ? v.topcate=="man"  false)
        (check[2] ? v.topcate == "kids" : false) || //check[2] ? v.topcate=="kids"  false)
        (check[3] ? v.topcate == "home" : false)) //check[3] ? v.topcate=="home"  false)
    )
      return true;

    // if (newVal.indexOf(key) !== -1) return true;
    // .indexOf()   
    //    -1 
    //  -1  true 
    // filter    !
  }); ////////////////filter////////////////////////

  // console.log("newList?", newList);
  /*     
        .filter(v=>{
            if(v..indexOf()!=-1) retrun true   // indexOf   ?  
        })
        ->      .
        ->filter  ,   .   .    
    */

  //[[ ]]// //////////////////////////////////////////////////////////////////////////////
  //(1)  
  if (sort == "newpd") {
    newList.sort(
      (
        a,
        b //   
      ) => (a.idx > b.idx ? -1 : a.idx < b.idx ? 1 : 0)
      //a.idx > b.idx ?  1()  a.idx < b.idx ?  -1()
    );
  }

  //(2) 
  else if (sort == "uppd") {
    newList.sort(
      (
        a,
        b //   
      ) => (a.name > b.name ? 1 : a.name < b.name ? -1 : 0)
      //a.name > b.name ?  1()  a.name < b.name ?  -1()
    );
  }
  //(3) 
  else if (sort == "downpd") {
    newList.sort(
      (
        a,
        b //   
      ) => (a.name > b.name ? -1 : a.name < b.name ? 1 : 0)
    );
  }
  //(4) 
  else if (sort == "chpd") {
    newList.sort(
      (
        a,
        b //   
      ) =>
        (a.price[2] ? a.price[2] : a.price[0]) >
        (b.price[2] ? b.price[2] : b.price[0])
          ? 1
          : (a.price[2] ? a.price[2] : a.price[0]) <
            (b.price[2] ? b.price[2] : b.price[0])
          ? -1
          : 0
      //a.price > b.price ?  1()  a.price < b.price ?  -1()
    );
  }
  //(5) 
  else if (sort == "expd") {
    newList.sort(
      (
        a,
        b //   
      ) =>
        (a.price[2] ? a.price[2] : a.price[0]) >
        (b.price[2] ? b.price[2] : b.price[0])
          ? -1
          : (a.price[2] ? a.price[2] : a.price[0]) <
            (b.price[2] ? b.price[2] : b.price[0])
          ? 1
          : 0
      //a.price > b.price ?  1()  a.price < b.price ?  -1()
    );
  } ////////////////////////////////////////////////////////////////////////////////////////////////////

  //   ////////////////////////
  return (
    <>
      <div className="sticky-search-box">
        {/* /////////////////// : ,,,HOME/////////////////// */}
        <div className="catebox">
          {mybutton.map((v, i) => (
            <button
              key={i}
              onClick={() => {
                setOnbutton(v);
                let temp = [false, false, false, false];
                temp[i] = true;
                setCheck(temp);
              }}
              className={onbutton == v ? "on" : ""}
            >
              {v}
            </button>
          ))}
        </div>

        <div className="resultandsort">
            {/*  //////////////////////  ?? /////////////////////////////////// */}
            <p >
                {newList.length}&nbsp;&nbsp;|&nbsp;&nbsp;
            </p>
            {/* ///////////////////sort//////////////////////////////////////// */}
            <div className="listbx">
              <aside className="sortbx">
                <select
                  name="sel"
                  id="sel"
                  className="sel"
                  onChange={(e) => {
                    setSort(e.target.value);
                  }}
                >
                  <option value="newpd"></option>
                  <option value="uppd"></option>
                  <option value="downpd"></option>
                  <option value="expd"></option>
                  <option value="chpd"></option>
                </select>
              </aside>
            </div>
        </div>

        {/* ///////////////////////////////////////////////////////// ///*/}
        <div className="search-module">
          <input
            id="schin"
            type="text"
            placeholder=", ,   "
            defaultValue={kword}
            //  !
            //     !
            // ->setKw()
            onKeyUp={(e) => {
              if (e.key == "Enter")
                //  
                setSearchword(e.target.value);
              //input  value

              // ()  
              setCheck([true, true, true, true]);
            }}
          />
            </div>
            </div>
          <SearchModuleList data={newList} />
        
      
    </>
  );
}

export default SearchModule;
