import { useEffect } from "react";
import Banner from "../modules/Banner";
import Footer from "../modules/Footer";

export default function Man() {
  useEffect(() => {
    window.scrollTo(0, 0);
  },[]);
  return (
    <>
      <Banner category="manData" />
      {/*    */}
      <Footer />
    </>
  );
}
