import React from "react";
//box2  
import { footerbox2data } from "../data/footer_area_data";
// css
import "../../css/footer.scss";

const Footer = () => {
  return (
    <>
      <div className="footer-area">
        <div className="footer-box1">
          {/* 11111111111111111111111111111111111111111111111111111111111111* */}

          <h3> </h3>
          <div className="footer-input">
            <input type="text" id="finput" className="finput" placeholder=" " />
            {/* css  '+'   input label  . */}
            <label htmlFor="finput">   </label>
          </div>
          <ul className="sns">
            {footerbox2data[1].map((v, i) => (
              i > 1 &&
              <li key={i}> <a href={v.link}>{v.txt}</a></li>
            ))}
          </ul>
        </div>

        <div className="footer-box2">
          {/* 222222222222222222222222222222222222222222222222222222222222222222222222 */}

          <ul style={{ display: "flex" }}>
            {footerbox2data.map((v, i) => (
              <li key={i}>
                <ol>
                  {v.map((inv, ini) => (
                    <li key={ini} className="navtxt">
                      {ini == 0 ? inv.txt : <a href={inv.link}>{inv.txt}</a>}
                    </li>
                  ))}
                </ol>
              </li>
            ))}
          </ul>
        </div>
        <div className="footer-box3">
          {/* 3333333333333333333333333333333333333333333333333333333333333333333333333 */}

          <p>
             : 120-88-14733   : ROMAY DE LA COLINA JOSE MANUEL     511 (,  33)   : 080-479-0880      : ITX Merken, B.V.     : 2014--02297 (<a target="_blank" href="http://www.ftc.go.kr/bizCommPop.do?wrkr_no=1208814733"></a>)   <a target="_blank" href="https://static.zara.net/static//pdfs/KR/privacy-policy/privacy-policy-ko_KR-20240524.pdf"></a> | <a target="_blank" href="https://static.zara.net/static//pdfs/KR/terms-and-conditions/terms-and-conditions-ko_KR-20240311.pdf"></a>
          </p>
        </div>
      </div>
    </>
  );
};

export default Footer;
