// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";

// Import Swiper styles
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";
import 'swiper/css/effect-creative';
//  css 
import "../plugin/css/swiper_main.scss";

// import required modules
import { Pagination, Navigation, Autoplay, Mousewheel,EffectCreative } from "swiper/modules";
//   
import { main_img } from "../data/main_img";
import { useRef } from "react";
import { Link } from "react-router-dom";

export default function SwiperMain() {

  //       
  //    
  const xx = useRef([]);

  return (
    <>
      {/*   ////////////////////////////////////////////////////////////////////////////////////////////////////////////// */}
      <Swiper
        className="mySwiper swiper-h"
        spaceBetween={0}
        // pagination={{
        //   clickable: true,
        // }}
        navigation={true}
        //  
        initialSlide={1}
        modules={[Autoplay, Pagination, Navigation, Mousewheel]}
        onSlideChange={(iam)=>{
          let idx = iam.activeIndex;
          let tg = xx.current;
          console.log(idx);
         /*  (4)   autoplay */
          tg.forEach((v,i)=>{
            if(i == idx) tg[i].autoplay.start();
            else tg[i].autoplay.stop();
          });
          
        }}
      >
          {/*   ////////////////////////////////////////////////////////////////////////////////////////////////////////////// */}
        {/* woman********************************************************** */}
          
        <SwiperSlide>

          <Swiper
            className="mySwiper2 swiper-v"
            direction={"vertical"}
            spaceBetween={0}
            mousewheel={true}
            // onInit()     
            onInit={(iam)=>xx.current.push(iam)}
             /* sticky      */
             effect={"creative"}
             creativeEffect={{              
               prev: {
                 shadow: true,
                 translate: [0, '-20%', -1],
                },
                next: {
                  translate: [0, '100%', 0],
                },
              }}
              // autoplay={{
            //   delay: 2000,
            //   disableOnInteraction: false,
            //   //   
            //   stopOnLastSlide: true,
            // }}
            // pagination={{
            //   clickable: true,
            // }}
            // navigation={true}
            modules={[Autoplay, Pagination, Navigation, Mousewheel,EffectCreative]}
            >
            {main_img.main_woman.map((v, i) => (
              <SwiperSlide key={i}>
                <img src={process.env.PUBLIC_URL+v.isrc} alt={v.tit} />
              </SwiperSlide>
            ))}
          </Swiper>
        </SwiperSlide>
         
          {/* man********************************************************** */}
        <SwiperSlide>
          <Swiper
            className="mySwiper2 swiper-v"
            direction={"vertical"}
            spaceBetween={0}
            mousewheel={true}
            onInit={(iam)=>xx.current.push(iam)}
            
            /* sticky      */
            effect={"creative"}
            creativeEffect={{              
              prev: {
                shadow: true,
                translate: [0, '-20%', -1],
              },
              next: {
                translate: [0, '100%', 0],
              },
            }}
            
            // autoplay={{
            //   delay: 2000,
            //   disableOnInteraction: false,
            //   //   
            //   stopOnLastSlide: true,
            // }}
            // pagination={{
            //   clickable: true,
            // }}
            // navigation={true}
            modules={[Autoplay, Pagination, Navigation, Mousewheel, EffectCreative]}
            >
            {main_img.main_man.map((v, i) => (
              <SwiperSlide key={i}>
                <img src={process.env.PUBLIC_URL+v.isrc} alt={v.tit} />
              </SwiperSlide>
            ))}
          </Swiper>
        </SwiperSlide>
            {/* kids********************************************************** */}
        <SwiperSlide>
          <Swiper
            className="mySwiper2 swiper-v"
            direction={"vertical"}
            spaceBetween={0}
            mousewheel={true}
            onInit={(iam)=>xx.current.push(iam)}
             /* sticky      */
             effect={"creative"}
             creativeEffect={{              
               prev: {
                 shadow: true,
                 translate: [0, '-20%', -1],
               },
               next: {
                 translate: [0, '100%', 0],
               },
             }}
            // autoplay={{
              //   delay: 2000,
              //   disableOnInteraction: false,
              //   //   
              //   stopOnLastSlide: true,
              // }}
              pagination={{
                clickable: true,
              }}
              // navigation={true}
              modules={[Autoplay, Pagination, Navigation, Mousewheel, EffectCreative]}
              >
            {main_img.main_kids.map((v, i) => (
              <SwiperSlide key={i}>
                <img src={process.env.PUBLIC_URL+v.isrc} alt={v.tit} />
              </SwiperSlide>
            ))}
          </Swiper>
        </SwiperSlide>
            {/* home********************************************************** */}
        <SwiperSlide>
          <Swiper
            className="mySwiper2 swiper-v"
            direction={"vertical"}
            spaceBetween={0}
            mousewheel={true}
            onInit={(iam)=>xx.current.push(iam)}
             /* sticky      */
             effect={"creative"}
             creativeEffect={{              
               prev: {
                 shadow: true,
                 translate: [0, '-20%', -1],
               },
               next: {
                 translate: [0, '100%', 0],
               },
             }}
            // autoplay={{
            //   delay: 2000,
            //   disableOnInteraction: false,
            //   //   
            //   stopOnLastSlide: true,
            // }}
            // pagination={{
            //   clickable: true,
            // }}
            // navigation={true}
            modules={[Autoplay, Pagination, Navigation, Mousewheel, EffectCreative]}
          >
            {main_img.main_home.map((v, i) => (
              <SwiperSlide key={i}>
                <img src={process.env.PUBLIC_URL+v.isrc} alt={v.tit} />
              </SwiperSlide>
            ))}
          </Swiper>
        </SwiperSlide>
      </Swiper>
    </>
  );
}
