// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";

// Import Swiper styles
import "swiper/css";
// import "swiper/css/pagination";
import "swiper/css/navigation";
import "swiper/css/effect-cube";
//  css 
import "./css/swiper_main.scss";
import "./css/swiper_main2.scss";

// import required modules
import {
    Pagination,
    Navigation,
    Autoplay,
    Mousewheel,
    EffectCube,
} from "swiper/modules";
//   
import { main_img, main_img_midea } from "../data/main_img";
import { useRef } from "react";

export default function SwiperMain() {
    //       
    //    
    const xx = useRef([]);

    return (
        <>
            {/*   ///////////////////////////////////////////////////////////// */}
            <Swiper
                className="mySwiper swiper-h"
                mousewheel={false}
                spaceBetween={0}
                // pagination={{
                //     clickable: true,
                // }}
                navigation={true}
                //  
                initialSlide={1}
                effect={"cube"}
                /*  **************** */
                cubeEffect={{
                    shadow: false,
                    slideShadows: true,
                    shadowOffset: 20,
                    shadowScale: 0.94,
                }}
                modules={[
                    Autoplay,
                    Pagination,
                    Navigation,
                    Mousewheel,
                    EffectCube,
                ]}
                onSlideChange={(iam) => {
                    let idx = iam.activeIndex;
                    let tg = xx.current;
                    console.log(idx);
                    /*  (4)   autoplay */
                    tg.forEach((v, i) => {
                        if (i == idx) tg[i].autoplay.start();
                        else tg[i].autoplay.stop();
                    });
                    // tg[1].autoplay.start();
                }}
            >
                {/* woman/////////////////////////////////////////////// */}
                <SwiperSlide>
                    {/*   ///////////////////////////////////////////////////////////// */}
                    <Swiper
                        className="mySwiper2 swiper-v"
                        mousewheel={false}
                        direction={"vertical"}
                        spaceBetween={0}
                        // onInit()     
                        onInit={(iam) => xx.current.push(iam)}
                        // autoplay={{
                        //   delay: 2000,
                        //   disableOnInteraction: false,
                        //   //   
                        //   stopOnLastSlide: true,
                        // }}
                        // pagination={{
                        //     clickable: true,
                        // }}
                        effect={"cube"}
                        grabCursor={true}
                        /*  **************** */
                        cubeEffect={{
                            shadow: false,
                            slideShadows: true,
                            shadowOffset: 20,
                            shadowScale: 0.94,
                        }}
                        // navigation={true}

                        modules={[
                            Autoplay,
                            Pagination,
                            Navigation,
                            Mousewheel,
                            EffectCube,
                        ]}
                    >
                        {main_img_midea.main_woman.map((v, i) => (
                            <SwiperSlide key={i}>
                                <img src={process.env.PUBLIC_URL+v.isrc} alt={v.tit} />
                            </SwiperSlide>
                        ))}
                    </Swiper>
                </SwiperSlide>

                {/* man/////////////////////////////////////////////// */}
                <SwiperSlide>
                    <Swiper
                        className="mySwiper2 swiper-v"
                        mousewheel={false}
                        direction={"vertical"}
                        spaceBetween={0}
                        onInit={(iam) => xx.current.push(iam)}
                        grabCursor={true}
                        effect={"cube"}
                        /*  **************** */
                        cubeEffect={{
                            shadow: false,
                            slideShadows: true,
                            shadowOffset: 20,
                            shadowScale: 0.94,
                        }}
                        // autoplay={{
                        //   delay: 2000,
                        //   disableOnInteraction: false,
                        //   //   
                        //   stopOnLastSlide: true,
                        // }}
                        // pagination={{
                        //     clickable: true,
                        // }}
                        // navigation={true}

                        modules={[
                            Autoplay,
                            Pagination,
                            Navigation,
                            Mousewheel,
                            EffectCube,
                        ]}
                    >
                        {main_img_midea.main_man.map((v, i) => (
                            <SwiperSlide key={i}>
                                <img src={process.env.PUBLIC_URL+v.isrc} alt={v.tit} />
                            </SwiperSlide>
                        ))}
                    </Swiper>
                </SwiperSlide>
                {/* kids/////////////////////////////////////////////// */}
                <SwiperSlide>
                    <Swiper
                        className="mySwiper2 swiper-v"
                        mousewheel={false}
                        direction={"vertical"}
                        spaceBetween={0}
                        onInit={(iam) => xx.current.push(iam)}
                        // autoplay={{
                        //   delay: 2000,
                        //   disableOnInteraction: false,
                        //   //   
                        //   stopOnLastSlide: true,
                        // }}
                        // pagination={{
                        //     clickable: true,
                        // }}
                        effect={"cube"}
                        grabCursor={true}
                        /*  **************** */
                        cubeEffect={{
                            shadow: false,
                            slideShadows: true,
                            shadowOffset: 20,
                            shadowScale: 0.94,
                        }}
                        // navigation={true}

                        modules={[
                            Autoplay,
                            Pagination,
                            Navigation,
                            Mousewheel,
                            EffectCube,
                        ]}
                    >
                        {main_img_midea.main_kids.map((v, i) => (
                            <SwiperSlide key={i}>
                                <img src={process.env.PUBLIC_URL+v.isrc} alt={v.tit} />
                            </SwiperSlide>
                        ))}
                    </Swiper>
                </SwiperSlide>
                {/* home/////////////////////////////////////////////// */}
                <SwiperSlide>
                    <Swiper
                        className="mySwiper2 swiper-v"
                        mousewheel={false}
                        direction={"vertical"}
                        spaceBetween={0}
                        onInit={(iam) => xx.current.push(iam)}
                        // autoplay={{
                        //   delay: 2000,
                        //   disableOnInteraction: false,
                        //   //   
                        //   stopOnLastSlide: true,
                        // }}
                        // pagination={{
                        //     clickable: true,
                        // }}
                        effect={"cube"}
                        grabCursor={true}
                        /*  **************** */
                        cubeEffect={{
                            shadow: false,
                            slideShadows: true,
                            shadowOffset: 20,
                            shadowScale: 0.94,
                        }}
                        // navigation={true}

                        modules={[
                            Autoplay,
                            Pagination,
                            Navigation,
                            Mousewheel,
                            EffectCube,
                        ]}
                    >
                        {main_img_midea.main_home.map((v, i) => (
                            <SwiperSlide key={i}>
                                <img src={process.env.PUBLIC_URL+v.isrc} alt={v.tit} />
                            </SwiperSlide>
                        ))}
                    </Swiper>
                </SwiperSlide>
            </Swiper>
        </>
    );
}
