//  css ///
import "../../css/top_area.scss";

// 
import $ from "jquery";

/*   */
// import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
// import { faSearch } from "@fortawesome/free-solid-svg-icons";

// //////
import { Link, useNavigate } from "react-router-dom";
/* gnb  */
import { gnbData } from "../data/gnb_data";
import Logo from "../modules/Logo";
import { useEffect } from "react";

export default function TopArea() {
  /*  :   **************************************************/
  const goNav = useNavigate(); //useNavigate  :goNav

  //1.     
  const enterKey = (e) => {
    //e.keyCode , e.key  .
    // console.log(e.key, e.keyCode);
    if (e.key == "Enter") {
      let txt = $(e.target).val().trim(); //  :val(),trim()   
      console.log("txt", txt);
      //    ( )
      if (txt != "") {
        // txt  

        goSearch(txt);
        $(e.target).val("").blur();
        $("#schin").focus();
      }
    }
  };

  //1.     
  const goSearch = (txt) => {
    console.log(" ");

    goNav("search", { state: { keyword: txt } });
    $("#schin").focus();
  };

  useEffect(() => {
    /* gnb   useEffect **************************************************/
    let tArea = document.querySelector(".gnb");
    let gnb = document.querySelectorAll(
      ".gnb li:nth-child(n+2):nth-child(-n+5)" //li 2 5  , gnb woman,man,kids,home
    );
    gnb.forEach(
      (ele) =>
        (ele.onmouseover = () => {
          tArea.classList.add("on");
        })
    );
    tArea.onmouseout = () => {
      tArea.classList.remove("on");
    };
  }, []);

  //   /////////////
  return (
    <>
      {/* 1. */}
      <header className="top-area">
        {/*    */}

        {/*  GNB */}
        <nav className="gnb">
          <ul>
            {/* 1.   */}
            <li>
              <Link to="/">
                <Logo logoStyle="top" />
              </Link>
            </li>
            {/* 2. GNB   ///////////////////////////// */}
            {gnbData.map((v, i) => (
              <li key={i}>
                {/* gnb  */}
                {<Link to={v.link}>{v.txt}</Link>}
                {
                  //      /////////////////////
                  v.sub && (
                    <div className="smenu">
                      <aside className="smbx">
                        <div className="swrap">
                          <ol>
                            {v.sub.map((v, i) => (
                              <li key={i}>
                                {/* v.txt    */}
                                <Link
                                  to={v.link}
                                  state={{ data: v.data }}
                                  style={{
                                    color: v.txt == " " ? "red" : "",
                                  }}
                                >
                                  {v.txt}
                                </Link>
                              </li>
                            ))}
                            <li>
                              <Link to={v.link}>
                                <img src={process.env.PUBLIC_URL+v.src} alt={v.txt} />
                                <div className="smenu-right-last-pbox">
                                  <p>{v.imagetxt}</p>
                                </div>
                              </Link>
                            </li>
                          </ol>
                        </div>
                      </aside>
                    </div>
                  )
                }
              </li>
            ))}

            {/* gnb   */}
            <li>
              <div className="searchGnb">
                {/* <FontAwesomeIcon
                                    icon={faSearch}
                                    className="schbtnGnb"
                                    title="Open search"
                                /> */}
                <input
                  type="text"
                  name="schinGnb" /* name    , id   */
                  id="schinGnb"
                  placeholder=""
                  onKeyUp={enterKey}
                  
                />

                <span
                  className="gum"
                  style={{
                    whiteSpace: "nowrap",
                    marginLeft: "3px",
                  }}
                  onClick={(e) => {
                    let inp = e.target.previousElementSibling;
                    if (inp.value.trim() != "") {
                      goSearch(inp.value);
                      inp.value = "";
                    } else {
                      // alert(" !");
                      // inp.focus();
                      goSearch("");
                     

                    }
                  }}
                >
                  
                </span>
                <span
                  style={{
                    whiteSpace: "nowrap",
                    marginLeft: "10px",
                  }}
                >
                  
                </span>
                <span
                  style={{
                    whiteSpace: "nowrap",
                    marginLeft: "10px",
                  }}
                >
                  (0)
                </span>
              </div>
            </li>
          </ul>
        </nav>
      </header>
    </>
  );
}
